body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding: 0;
  background-color: white;
  overflow-x: hidden; /* Remove horizontal scrolling */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.frontimg {
  position: relative;
  width: 100%;
}

.maintext {
  position: absolute;
  top: 250px;
  left: 50%;
  transform: translateX(-50%);
  color: white;
  font-weight: 900;
  text-shadow: 0 0 3px #F10D58, 0 0 7px #4578D5;
  font-size: 2rem;
  text-align: center;
}

.maintext2 {
  position: absolute;
  top: 300px;
  left: 50%;
  transform: translateX(-50%);
  color: white;
  font-weight: 900;
  text-shadow: 0 0 3px #F10D58, 0 0 7px #4578D5;
  font-size: 1.5rem;
  text-align: center;
}

.separation {
  height: 7px;
  background-color: rgb(46, 44, 44);
}

.maincont {
  background-color: white;
  height: 90vh;
  position: relative;
}

.container {
  height: auto;
  position: relative;
  margin: 2rem auto;
  padding: 1rem;
  max-width: 80%;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  background-color: #e8f48c;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  gap: 20px;
}

.container .icon {
  border: 1px solid black;
  border-radius: 50%;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.icontext {
  color: black;
}

.card-section {
  height: auto;
  padding: 2rem;
}

.servtext {
  text-align: center;
  margin-top: 2rem;
  letter-spacing: 4px;
  font-weight: bold;
  font-size: 1.5rem;
}

.items {
  width: 90%;
  max-width: 1000px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 30px;
  margin: 2rem auto;
}

.card {
  border: 1px solid #ddd;
  padding: 1rem;
  border-radius: 8px;
  background-color: #fff;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.card:hover {
  transform: scale(1.05);
  box-shadow: rgb(38, 57, 77) 0px 20px 30px -10px;
}

.chat-section {
  height: auto;
  padding: 2rem;
}

.chat-container {
  width: 90%;
  max-width: 1000px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 20px;
  margin: 2rem auto;
}

.chathead {
  text-align: center;
  margin-top: 2rem;
}

.chat-text h1 {
  font-size: 2rem;
  margin-bottom: 1rem;
}

.chat-text p {
  font-size: 1rem;
  margin-bottom: 1rem;
}

.chat-text button {
  color: white;
  padding: 10px 30px;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.chat-text button:hover {
  background-color: #0056b3;
}

.login-section {
  height: auto;
  padding: 2rem;
}

.log-top {
  text-align: center;
  margin-top: 2rem;
}

.login-items {
  width: 90%;
  max-width: 600px;
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin: 2rem auto;
  align-items: center;
  justify-content: center;
}

.login-items h1 {
  font-size: 2rem;
}

.login-items p {
  font-size: 1rem;
}

.login-items button {
  padding: 10px 40px;
  margin-left: 10px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.login-items button:hover {
  background-color: #0056b3;
}

/* Media Queries for Responsiveness */
@media (max-width: 768px) {
  .maintext, .maintext2 {
    top: 200px;
    font-size: 1.2rem;
    left: 50%;
    transform: translateX(-50%);
  }

  .container, .items, .chat-container, .login-items {
    width: 100%;
    margin: 1rem;
    padding: 1rem;
  }

  .items {
    flex-direction: column;
  }

  .card {
    width: 100%;
  }

  .chat-text h1, .login-items h1 {
    font-size: 1.5rem;
  }

  .chat-text p, .login-items p {
    font-size: 0.9rem;
  }

  .chat-text button, .login-items button {
    padding: 10px 20px;
  }
}




